
body{
  background-color: #f3f3f3;
}

canvas{left:0;position:absolute;top:0
}
.net{height:100%;margin:0
}
.node{-webkit-transition:fill .5s ease;fill:#dcfaf3;stroke:rgba(18,120,98,.7);stroke-width:3px;transition:fill .5s ease
}
.node.selected{stroke:#caa455
}
.node.pinned{stroke:rgba(190,56,93,.6)
}
.link{stroke:rgba(18,120,98,.3)
}
.link,.node{stroke-linecap:round
}
.link:hover,.node:hover{stroke:#be385d;stroke-width:5px
}
.link.selected{stroke:rgba(202,164,85,.6)
}
.curve{fill:none
}
.link-label,.node-label{fill:#127862
}
.link-label{-webkit-transform:translateY(-.5em);text-anchor:middle;transform:translateY(-.5em)
}
